$("._sum-input").change(function () {
    let val = $(this).val()
    if(val < 150){
        $(this).parents(".fg").addClass("_error")
    } else{
        $(this).parents(".fg").removeClass("_error")
    }
})

$(".sum-field input").change(function () {
    setSum($(this))
})

function setSum(el) {
    let sum = +$(el).siblings("label").find("span").html()
    console.log(sum)
    $("._sum").html(sum)
}
